import { reportTypes } from "@/common/constants";
import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from "@/common/constants";
/**
 * reportUser 举报界面
 * @description 举报界面
 * @Author: 穆兰
 * @Date: 2022/1/7
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/7
 */
export default {
    data() {
        return {
            reportTypes,
            reportSelected: reportTypes[reportTypes.length - 1],
            reportReason: '',
            reportSuccess: false,
            objectId: -1,
            objectType: '' //举报对象的类型 动态为activity,评论为comment,私聊为chat,评论的回复是commentResponse
        };
    },
    onLoad(params) {
        this.objectId = params.objectId;
        this.objectType = params.objectType;
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description  选择举报的类型
         * @param {object} evt
         */
        onReportTypesChange(evt) {
            this.reportSelected = evt.detail.value;
        },
        /**
         * @description  输入时
         * @param {object} e
         */
        onTextInput(e) {
            this.reportReason = e.detail.value;
        },
        /**
         * @description  点击举报按钮
         */
        async onReportButtonClick() {
            if (this.reportSelected === "其他" && this.reportReason.length < 1) {
                uni.showToast({
                    title: '请先输入原因！',
                    icon: "none"
                });
                return;
            }
            this.reportSuccess = await this.reportUser(this.objectId, this.objectType, this.reportSelected + '-' + this.reportReason);
        },
        /**
         * @description  举报用户
         * @param {number} objectId 举报的id
         * @param {string} objectType 举报的类型
         * @param {string} reason 举报的原因
         * @return {boolean} 成功返回true失败返回false
         */
        async reportUser(objectId, objectType, reason) {
            console.log("objectId", objectId, "objectType", objectType, "reason", reason);
            let res = await request({
                data: {
                    method: 'POST',
                    group: 'user',
                    action: 'inform',
                    data: {
                        objectId: objectId,
                        objectType: objectType,
                        reason: reason
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                console.log(res.data);
                return true;
            }
            else {
                console.log('reportUser', res.data);
                return false;
            }
        }
    }
};
